import React from 'react';

const StatusPanel = (props: any) => {
  //  
  const { sensorData } = props;

  return (
    <div className='status-panel'>
      {sensorData.length > 0 ? (
        <React.Fragment>
          <div className='status-panel__item'>
            <span> : {sensorData[0].error_code}</span>
          </div>
          <div className='status-panel__item'>
            <span>
                : {sensorData[0].battery_voltage}
            </span>
          </div>
          <div className='status-panel__item'>
            <span> : {sensorData[0].battery_charge}%</span>
          </div>
        </React.Fragment>
      ) : (
        <div className='status-panel__item'>
          <span>      </span>
        </div>
      )}
    </div>
  );
};

export default StatusPanel;
